a {
  text-decoration: none;
  color: #1ec896;
}

img {
  width: 100%;
  height: auto;
}

.section {
  background-color: rgba(0, 0, 0, 0.2);
  /* background-color: rgba(230, 230, 230, 0.2); */
  width: 98.5%;
  max-width: 1400px;
  height: 100%;
  margin: 1rem auto;
  padding: 3.5rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
}

.section-title > h3 {
  text-align: center;
  width: 300px;
  font-size: 2.5rem;
  color: #212529;
  opacity: 0.8;
  margin-bottom: 0.4rem;
  padding: 0.3rem;
  border-image: linear-gradient(
      to right,
      transparent 0%,
      #1ec896 30%,
      #1ec896 50%,
      #1ec896 70%,
      transparent 100%
    )
    1;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  letter-spacing: 3px;
}

.section-title > h4 {
  text-align: center;
  width: 300px;
  font-size: 2rem;
  color: #212529;
  opacity: 0.5;
  letter-spacing: 3px;
}

.section-division {
  width: 98.5%;
  max-width: 1300px;
  height: 100%;
  margin: 3rem auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.section-division .section-division-line {
  width: 30%;
  height: 2px;
  background-color: #212529;
}

.section-division .section-division-circle {
  width: 5px;
  height: 5px;
  background-color: #212529;
  border-radius: 50%;
}
